.wave_group {
  position: fixed;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  border: 3px solid #12B7F5;
  box-shadow: 0 0 1rem #12B7F5;
  bottom: 0;
  left: 4.5%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  overflow: hidden;
  background-color: #fff;
  transition: all 1s ease; }

.wave {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: #12B7F5;
  border-radius: 50%;
  cursor: pointer; }
  .wave::after, .wave::before {
    content: "";
    position: absolute;
    width: 6rem;
    height: 6rem;
    top: 0;
    left: 50%;
    transition: height 1s ease;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 45%;
    transform: translate(-50%, -70%) rotate(0);
    -webkit-transform: translate(-50%, -70%) rotate(0);
    animation: wave 3s linear infinite;
    -webkit-animation: wave 3s linear infinite;
    z-index: 10; }
  .wave::after {
    border-radius: 47%;
    background-color: rgba(255, 255, 255, 0.6);
    transform: translate(-50%, -70%) rotate(0);
    -webkit-transform: translate(-50%, -70%) rotate(0);
    animation: wave 6s linear -5s infinite;
    -webkit-animation: wave 6s linear -5s infinite;
    z-index: 20; }
  .wave:hover::after, .wave:hover::before {
    height: 14rem; }

